import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-notfound',
    styleUrl: 'notfound.css'
})
export class NotFound {
    @Prop() link: string = "/";
    @Prop() linkName: string = "Strona Główna"
    @Prop() status: string = "404";

	render() {
		return [
            this.status && <div class="status">{this.status}</div>,
            <div class="content">
                <slot/>
            </div>,
            <ks-button round nofollow link={this.link} name={this.linkName} />
        ];
	}
}
